import React, { Component } from 'react';
import smoothscroll from 'smoothscroll-polyfill';

import './style.scss';
import i18n from '../../i18n';

smoothscroll.polyfill();

const BannerPres = ({ menu }) => {

  return (
    <div id="banner-div" className="banner-div fixed-top">
      <div className="banner-home"
        onClick={() => document.getElementById('welcome').scrollIntoView({
            behavior: 'smooth'
          })
        }>
        YK
      </div>
      <div className="banner-links">
        {menu}
      </div>
    </div>
  )
}

class Banner extends Component {

  menuItems = Object.entries(i18n.mainMenu).map(([key, val]) =>
      <span className="banner-link-items" key={key}>
        <a onClick={() =>
          document.getElementById(`${val}`).scrollIntoView({
            top: 500,
            behavior: 'smooth'
          })
        } href={`#${val}`}>
          {val}
        </a>
      </span>
    )

  handleScroll(e) {
    if (window.pageYOffset > 50) {
      //document.getElementById('banner-div').setAttribute('style', 'background-color: white; color: black;  transform: scaleY(1.1)');
    } else {
      document.getElementById('banner-div').setAttribute('style', 'background-color: black; color: white;');
    }
  }

  componentDidMount() {
    window.addEventListener('scroll', () => this.handleScroll(), true);
  }

  render() {
    return <BannerPres menu = {this.menuItems}/>;
  }
}

export default Banner;
