import React from 'react';

import i18n from '../../i18n';
import "./style.scss";

const About = () => {
  const loadText = () =>  Object.entries(i18n.aboutText).map(([key, elem]) => {
    const { id, title, body } = elem;
    return (
      <div key={key} className={id % 2 === 0 ? 'abt-even' : 'abt-odd'}>
        <h5>{title}</h5>
        {title === 'Skills' ?
          <p>hello</p>
          : <p>{body}</p>}
      </div>
    );
  })

  return (
    <div id="about">
      <h5>About Me</h5>
      {loadText()}
    </div>
  )
}

export default About;
