import React from 'react';

import './style.scss';
import i18n from '../../i18n';

const Contact = () => {
  const loadLinks = () => Object.entries(i18n.contactLink).map(([key, val]) => {
      const {link, type, img} = val;
      return (
        <div key={key} className="contact-links">
          <a href={link}><img src={require(`../../assets/${img}.png`)} alt={type}></img></a>
        </div>
      )}
    )

  return (
    <div id="contact">
      <div className="contact-title">
        I would love to hear from you!
        <br />
        Drop me a line or have a drink with me!
      </div>
      <div className="contact-link-cont">
        {loadLinks()}
      </div>
    </div>
  )
}


export default Contact;
