import { combineReducers } from "redux";

import { SWITCH_ROUTE } from "../actions/actionTypes";
import { TYPE_TEXT, DELETE_TEXT, CHANGE_INDEX, GET_PROJECT } from "../actions/actionTypes";
import i18n from "../i18n";


export const textReducer = (state = {
  index: 0,
  displayText: "",
  textArray: [...i18n.welcomeText]
}, action) => {
  switch (action.type) {
    case TYPE_TEXT:
      return { ...state, displayText: action.payload };
    case DELETE_TEXT:
      return { ...state, displayText: action.payload };
    case CHANGE_INDEX:
      return { ...state, index: action.payload };
    default:
      return state;
  }
};


const switchReducer = (state = null, action) => {
  switch (action.type) {
    case SWITCH_ROUTE:
      return {...state, selected: action.payload };
    default:
      return state;
  }
};

const projectReducer = (state = {}, action) => {
  switch (action.type) {
    case GET_PROJECT:
      return {...state, projectList: action.payload };
    default:
      return state;
  }
}


const rootReducer = combineReducers({
  switches: switchReducer,
  welcomeText: textReducer,
  projectList: projectReducer
});

export default rootReducer;
