import {
  SWITCH_ROUTE,
  TYPE_TEXT,
  DELETE_TEXT,
  CHANGE_INDEX,
  GET_PROJECT
} from "./actionTypes";

import axios from 'axios';

export const switchRoute = props => dispatch => {
  dispatch({ type: SWITCH_ROUTE, payload: props });
};

export const typeText = props => dispatch => {
  dispatch({ type: TYPE_TEXT, payload: props });
};

export const deleteText = props => dispatch => {
  //const text = props.substring(0, props.length);
  dispatch({ type: DELETE_TEXT, payload: props });
};

export const changeIndex = props => dispatch => {
  dispatch({ type: CHANGE_INDEX, payload: props });
};

export const getProjects = props => async dispatch => {
  const res = await axios.get('https://api.github.com/users/yohank94/repos');
  dispatch({ type: GET_PROJECT, payload: res.data });
}
